import React, { Component } from 'react';
import './DateRangePicker.css';
import { DateRange } from 'react-date-range';
import MaskedTextInput from 'react-text-mask';
import moment from 'moment';
import iconcalendar from './imgs/common/icon--calendar.svg';

class DateRangePicker extends Component {
	constructor(props){
		super(props);

		this.state = {
			showCalendar:false,
			rangePicker:{}
						}
		this.onclick = this.onclick.bind(this);
		this.handleOutsideClick = this.handleOutsideClick.bind(this);
		this.periodClick = this.periodClick.bind(this);
	}
	onclick(e){
		if (!this.state.showCalendar) {
			// attach/remove event handler
			document.addEventListener('click', this.handleOutsideClick, false);
		 } else {
			document.removeEventListener('click', this.handleOutsideClick, false);
		 }

		this.setState(prevState => ({
			showCalendar:!prevState.showCalendar})
		);
	}

	periodClick(e){
		console.log(e.target.value);
		
		let today = moment(new Date());
		
		if(e.target.value=="W"){
			this.setState({
				rangePicker : {
					startDate : moment(today).subtract(7, 'days'),
					endDate : today
				}
			});
		}
		if(e.target.value=="T"){
			this.setState({
				rangePicker : {
					startDate : today,
					endDate : today
				}
			});
		}
		if(e.target.value=="M"){
			
			this.setState({
				rangePicker : {
					startDate : moment(today).subtract(1, 'month'),
					endDate : today
				}
			});
		}
		if(e.target.value=="2M"){
			this.setState({
				rangePicker : {
					startDate : moment(today).subtract(2, 'month'),
					endDate : today
				}
			});
		}
		
	}

	handleOutsideClick(e) {
		// ignore clicks on the component itself
		if (this.node.contains(e.target)) {
		  return;
		}
		
		this.onclick();
	 }	

	 handleChange(which, payload) {
		this.setState({
		  [which] : payload
		});
	 }

	render(){
		const showCalendar = this.state.showCalendar;
		const rangePicker = this.state.rangePicker;
		const format = 'YYYY/MM/DD';
		return (
			<section>
			<div ref={node => { this.node = node; }}>

			<div class="flex lefty" >
			<div class="initial">
				 <div class="flex">
					  <div class="initial ipt-date ipt-date-start">
					  <MaskedTextInput className="form-control" mask={[/[1-2]/, /\d/, /\d/, /\d/, '/', /[0-1]/, /\d/, '/', /[0-3]/, /\d/]}  id="sales-management-date-start"  placeholder="시작일" onClick={this.onclick} value={ rangePicker['startDate'] && rangePicker['startDate'].format(format).toString()} />
					  </div>
					  <div class="initial">
							<button type="button" class="btn btn-calendar">
								 <img class="icon" src={iconcalendar} />
							</button>
					  </div>
					  
				 </div>
			</div>
			<div class="initial pl-2 pr-2 mark-to">~</div>
			<div class="initial">
				 <div class="flex">
					  <div class="initial ipt-date ipt-date-end">
					  <MaskedTextInput className="form-control" mask={[/[1-2]/, /\d/, /\d/, /\d/, '/', /[0-1]/, /\d/, '/', /[0-3]/, /\d/]}  id="sales-management-date-end"  placeholder="마지막일" onClick={this.onclick}  value={ rangePicker['endDate'] && rangePicker['endDate'].format(format).toString() } />
					  </div>
					  <div class="initial">
							<button type="button" class="btn btn-calendar">
							<img class="icon" src={iconcalendar} />
							</button>
					  </div>
				 </div>
			</div>
			<div class="default pl-3 lefty date-term-container">
				 <div class="button-group">
					  <button type="button" class="btn active mr-1" value="T" onClick={this.periodClick}> 
							당일
					  </button>
					  <button type="button" class="btn mr-1" value="W" onClick={this.periodClick}>
							1주
					  </button>
					  <button type="button" class="btn mr-1" value="M" onClick={this.periodClick}>
							1개월
					  </button>
					  <button type="button" class="btn" value="2M" onClick={this.periodClick}>
							2개월
					  </button>
				 </div>
			</div>
		</div>
		{showCalendar && <DateRange
			onInit={this.handleChange.bind(this, 'rangePicker')}
			onChange={this.handleChange.bind(this, 'rangePicker')}
			calendars="1"
			lang={"ko"}
			theme={{
				DateRange : { width: 280 }
			 }}			
			/>}			
			
			
			
		</div>
		<div>
		<p>테스트테스트테스트테스트테스트테스트테스트</p>
		<p>테스트테스트테스트테스트테스트테스트테스트</p>
		<p>테스트테스트테스트테스트테스트테스트테스트</p>
		<p>테스트테스트테스트테스트테스트테스트테스트</p>
		<p>테스트테스트테스트테스트테스트테스트테스트</p>
		<p>테스트테스트테스트테스트테스트테스트테스트</p>
		<p>테스트테스트테스트테스트테스트테스트테스트</p>
		<p>테스트테스트테스트테스트테스트테스트테스트</p>
		<p>테스트테스트테스트테스트테스트테스트테스트</p>
		
		</div>
		</section>
		)
	}
}

export default DateRangePicker;


